"use client";

import React, { useEffect } from "react";
import { motion, useAnimation, useScroll, useTransform } from "framer-motion";
import { useInView } from "react-intersection-observer";

const StapItem = ({ letter, titel, beschrijving, index }) => {
  const controls = useAnimation();
  const [ref, inView] = useInView({
    threshold: 0.3,
    triggerOnce: false,
  });

  // Parallax effect op de letter cirkel
  const { scrollYProgress } = useScroll({
    target: ref,
    offset: ["start end", "end start"],
  });

  const yOffset = useTransform(scrollYProgress, [0, 1], [50, -50]);
  const opacity = useTransform(
    scrollYProgress,
    [0, 0.2, 0.6, 0.8],
    [0, 1, 1, 0.8]
  );
  const scale = useTransform(
    scrollYProgress,
    [0, 0.2, 0.8, 1],
    [0.8, 1, 1, 0.9]
  );

  useEffect(() => {
    if (inView) {
      controls.start("visible");
    } else {
      controls.start("hidden");
    }
  }, [controls, inView]);

  const isLast = index === 3;

  return (
    <motion.div
      ref={ref}
      initial="hidden"
      animate={controls}
      className={`relative w-full md:w-[25vw] ${
        !isLast ? "border-r border-gray-300" : ""
      } px-[3vw] py-[4vw]`}
      style={{
        opacity,
        scale,
      }}
    >
      <motion.div
        className="absolute top-[4vw] left-[3vw]"
        style={{ y: yOffset }}
        whileHover={{ scale: 1.1, transition: { duration: 0.2 } }}
      >
        <div className="rounded-full w-[5vw] h-[5vw] max-w-[50px] max-h-[50px] flex items-center justify-center border border-black">
          <span className="font-medium text-[2vw] md:text-[1.2vw]">
            {letter}
          </span>
        </div>
      </motion.div>

      <motion.div
        className="mt-[10vw]"
        variants={{
          hidden: { opacity: 0, y: 20 },
          visible: {
            opacity: 1,
            y: 0,
            transition: {
              duration: 0.6,
              delay: index * 0.1,
              ease: "easeOut",
            },
          },
        }}
      >
        <motion.h2
          className="text-[5vw] md:text-[3vw] font-black mb-[1vw]"
          variants={{
            hidden: { opacity: 0, x: -20 },
            visible: {
              opacity: 1,
              x: 0,
              transition: {
                duration: 0.5,
                delay: 0.1 + index * 0.1,
              },
            },
          }}
        >
          {titel}
        </motion.h2>

        <motion.p
          className="text-[2.5vw] md:text-[1.5vw]"
          variants={{
            hidden: { opacity: 0, x: -10 },
            visible: {
              opacity: 1,
              x: 0,
              transition: {
                duration: 0.5,
                delay: 0.2 + index * 0.1,
              },
            },
          }}
        >
          {beschrijving}
        </motion.p>
      </motion.div>
    </motion.div>
  );
};

const VierStappenComponent = () => {
  const { scrollYProgress } = useScroll();
  const backgroundColor = useTransform(
    scrollYProgress,
    [0, 0.5, 1],
    [
      "rgba(245, 245, 245, 0.9)",
      "rgba(245, 245, 245, 0.95)",
      "rgba(245, 245, 245, 1)",
    ]
  );

  // Data voor de stappen
  const stappen = [
    {
      letter: "A",
      titel: "AMBITIE",
      beschrijving: "analyseren we en wordt concreet",
    },
    {
      letter: "B",
      titel: "AANPAK",
      beschrijving: "blijft bijgestelt worden",
    },
    {
      letter: "C",
      titel: "SBD",
      beschrijving: "gaat vooruitgang in komen",
    },
    {
      letter: "D",
      titel: "REFLECT",
      beschrijving: "doen we om stappen te blijven maken",
    },
  ];

  return (
    <motion.div
      className="relative w-[100vw] min-h-[100vh] py-[10vw]"
      style={{ backgroundColor }}
    >
      {/* Achtergrondafbeelding */}
      <div
        className="absolute inset-0 bg-center bg-cover bg-no-repeat opacity-90"
        style={{ backgroundImage: "url('/achtergrond.png')" }}
      ></div>

      {/* Header met animatie */}
      <motion.div
        className="relative z-10 w-[80vw] mx-auto mb-[5vw]"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
      >
        <h1 className="text-[6vw] md:text-[4vw] font-black text-center">
          ONS PROCES
        </h1>
      </motion.div>

      {/* Content container */}
      <div className="relative z-10 w-[90vw] mx-auto">
        {/* Flex container voor de kolommen */}
        <div className="flex flex-col md:flex-row w-full">
          {stappen.map((stap, index) => (
            <StapItem
              key={stap.letter}
              letter={stap.letter}
              titel={stap.titel}
              beschrijving={stap.beschrijving}
              index={index}
            />
          ))}
        </div>
      </div>
    </motion.div>
  );
};

export default VierStappenComponent;
